	module.exports = {"data":{"markdownRemark":{"html":"<p>그는 이른 아침 마오 쩌둥 장관을 읽고 마오 쩌둥 (Hu Qiaomu), 첸보다 (Chen Boda), 티안 지아 징 (Tian Jiaying), 장쩌민 (Jiang Qing), 가오 지 (Gao Zhi), 루오 루앙 (Luo Luguang) 등 6 명의 비서관에게 편지를 썼다. . 마오 쩌둥 (Mao Zedong)의시에 대한 논평을 공개한다 : \"시를 쓰는 것은 어렵고, 쓰는 것은 쉽지 않으며, 경험자는 물고기와 같고 추위와 따뜻한 자기 지식은 외부인에게 충분하지 않다.\"시인의 깊은 경험이다.</p>\n<p>오후에는 컴퓨터가 바이러스가되어 수십만 단어 중 하나가 파괴되었습니다. 백업이 있기를 바랍니다. 그렇지 않으면 비극적 인 일이 될 것입니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0168.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0168.md","frontmatter":{"title":"10월 26일 수요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0168.md absPath of file >>> MarkdownRemark"}}

